import React, {
  useRef,
  useState,
  InputHTMLAttributes,
  useCallback,
  useEffect,
  useMemo
} from "react";
import { Link } from "react-router-dom";

import * as Common from "shock-common";
import c from "classnames";

import * as Store from "../../store";
import Pad from "../Pad";
import * as Utils from "../../utils";
import * as globalStyles from "../../styles";

import "./css/index.scoped.css";

export interface ShockAvatarProps {
  height: number;
  publicKey: string;
  disableOnlineRing?: boolean;
  nameAtBottom?: boolean;
  /**
   * If true, will have the user select the avatar on press. Will disable status
   * ring.
   */
  setsAvatar?: boolean;
  /**
   * If true, avatar will have a thin grey border around it. Will override
   * status/online rings.
   */
  greyBorder?: boolean;
  /**
   * If true, will force show the add button even if there stories present.
   * Recommended to use together with disableStoriesRing.
   */
  forceAddBtn?: boolean;
  /**
   * If true, will link to create post.
   */
  createsPost?: boolean;
}

const ShockAvatar: React.FC<ShockAvatarProps> = ({
  height,
  publicKey,
  disableOnlineRing,
  nameAtBottom,
  setsAvatar,
  greyBorder,
  forceAddBtn,
  createsPost
}) => {
  const avatarImageFileInput = useRef<HTMLInputElement>(null);
  const [settingAvatar, setSettingAvatar] = useState<boolean>(false);
  const { lastSeenApp, lastSeenNode } = Utils.useLastSeen(publicKey);
  const selfPublicKey = Store.useSelector(Store.selectSelfPublicKey);
  const isSelf = publicKey === selfPublicKey;

  const { forceUpdate } = Utils.useForceUpdate();
  const { avatar: image, displayName } = Store.useSelector(
    Store.selectUser(publicKey)
  );

  useEffect(() => {
    const intervalID = setInterval(() => {
      forceUpdate();
    }, Math.min(Common.LAST_SEEN_APP_INTERVAL, Common.LAST_SEEN_NODE_INTERVAL));

    return () => {
      clearInterval(intervalID);
    };
  }, [forceUpdate]);

  const avatarStyle = useMemo(() => {
    const style: React.CSSProperties = {
      height,
      width: height,
      borderRadius: "50%",
      objectFit: "cover"
    };

    if (greyBorder) {
      style.borderWidth = "2px";
      style.borderStyle = "solid";
      style.borderColor = "grey";
    }

    if (!disableOnlineRing && Common.isNodeOnline(lastSeenNode)) {
      style.borderWidth = 2;
      style.borderStyle = "solid";
      style.borderColor = "#AD5C00";
    }
    // green ring takes precedence over amber ring
    if (!disableOnlineRing && Common.isAppOnline(lastSeenApp)) {
      style.borderWidth = 2;
      style.borderStyle = "solid";
      style.borderColor = "#39B54A";
    }

    return style;
  }, [height, lastSeenNode, lastSeenApp, disableOnlineRing, greyBorder]);

  // const showStoryRing = story.length && !onPressProp && !setsAvatar;

  // if (showStoryRing) {
  //   avatarStyle.borderWidth = 2;
  //   avatarStyle.borderStyle = "dotted";
  //   avatarStyle.borderColor = "#39B54A"
  // }

  const onSelectedAvatarFile: InputHTMLAttributes<{}>["onChange"] = async e => {
    try {
      e.preventDefault();
      if (settingAvatar) {
        return;
      }

      setSettingAvatar(true);

      const { files } = (e.target as unknown) as {
        files: readonly Utils.File[];
      };

      if (files.length === 0) {
        return;
      }

      if (files.length !== 1) {
        Utils.logger.error(`ShockAvatar -> files.length !== 1`);
        alert(
          `An error occurred while trying to set an avatar. This has been logged.`
        );
        return;
      }

      const [file] = files;

      const imageObtained = await Utils.processImageFile(file, 320, 0.7);

      const DATA_URL_TYPE_PREFIX = "data:image/jpeg;base64,";
      const base64 = imageObtained.slice(DATA_URL_TYPE_PREFIX.length);

      await Utils.Http.post(`/api/gun/put`, {
        path: "$user>profileBinary>avatar",
        value: base64
      });
    } catch (e) {
      Utils.logger.error(`Error while trying to load new avatar`);
      Utils.logger.error(e);
      alert(
        "There was an error loading the new avatar, this has error has been logged."
      );
    } finally {
      setSettingAvatar(false);
    }
  };

  const onPress = useCallback(
    e => {
      if (setsAvatar) {
        e.preventDefault();
        const { current } = avatarImageFileInput;
        if (!current) {
          Utils.logger.error("File input element for avatar is falsy.");
        }
        current.click();
      }
    },
    [setsAvatar]
  );

  return (
    <>
      <Link
        className={c(
          globalStyles.colCentered,
          globalStyles.relative,
          "container"
        )}
        onClick={onPress}
        to={(() => {
          if (createsPost) {
            return "/createPost";
          }
          if (isSelf) {
            return "/profile";
          }
          return `/otherUser/${publicKey}`;
        })()}
      >
        <img
          alt={`Avatar for ${displayName || "an user"}`}
          src={`data:image/jpeg;base64,${image || DEFAULT_USER_IMAGE}`}
          style={avatarStyle}
        />

        {nameAtBottom && (
          <>
            <Pad amt={12} />

            <span className="name-at-bottom">{displayName}</span>
          </>
        )}

        {forceAddBtn && (
          <div className="user-add-btn">
            <i className="fas fa-plus" />
          </div>
        )}

        {setsAvatar && (
          <i
            className={c(
              globalStyles.absoluteDeadCenter,
              "fas",
              "fa-pencil-alt",
              "pencil-icon"
            )}
          />
        )}
      </Link>

      <input
        type="file"
        id="avatar-file"
        ref={avatarImageFileInput}
        hidden
        accept="image/*"
        onChange={onSelectedAvatarFile}
      />
    </>
  );
};

const DEFAULT_USER_IMAGE =
  "";

export default ShockAvatar;
